* {
  box-sizing: border-box;
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

ul {
  list-style: none;
}

ul li {
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0;
}

body, html {
  background: #ffffff;
  color: #373737;
  font-family: 'Geometria', Arial, sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page__content {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  padding-top: 150px;
  padding-bottom: 80px;
}

@media only screen and (max-width: 1336px) {
  .page__content {
    padding-top: 106px;
  }
}

@media only screen and (max-width: 992px) {
  .page__content {
    padding-top: 64px;
  }
}

@media only screen and (max-width: 788px) {
  .page__content {
    padding-top: 55px;
    padding-bottom: 40px;
  }
}

.container {
  max-width: 1320px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
}

@media only screen and (max-width: 788px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

input {
  -moz-appearance: none;
  -webkit-appearance: none;
}

.page {
  overflow: hidden;
}

.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px;
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
}

.btn {
  font-size: 16px;
  padding: 15px;
  max-height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: normal;
  background: #f06eaa;
  color: #fff;
  transition: all 0.25s ease-in-out;
  font-family: "Futura PT Book";
  border-color: #f06eaa;
}

.btn:hover {
  background: #de619b;
  border-color: #de619b;
}

.input-container {
  position: relative;
  font-size: 0;
}

.input-container.has-error label {
  position: absolute;
  font-size: 12px;
  color: #f06eaa;
  font-family: "Futura PT Book";
  background: #fff;
  bottom: -15px;
  right: 0;
}

.input-container.has-error input,
.input-container.has-error textarea {
  border-color: #f06eaa !important;
