.input-container textarea::-moz-placeholder {
  color: #959595;
}

.input-container input:-ms-input-placeholder,
.input-container textarea:-ms-input-placeholder {
  color: #959595;
}

.input-container input:-moz-placeholder,
.input-container textarea:-moz-placeholder {
  color: #959595;
}

.input-container input.btn,
.input-container textarea.btn {
  background: #f06eaa;
  border-color: #f06eaa;
  cursor: pointer;
  font-family: "Futura PT";
  color: #fff;
  font-size: 15px;
}

.input-container input.btn:hover,
.input-container textarea.btn:hover {
  background: #de619b;
  border-color: #de619b;
}

.ui-checkbox {
  position: relative;
}

.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #904c96;
}

.ui-checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
  padding-left: 26px;
  font-size: 16px;
  color: #000;
  font-family: "Futura PT Book";
  letter-spacing: 0.8px;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 16px;
  background-color: white;
  border: 1px solid #e1e1e1;
  border-radius: 50%;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 4px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 10px;
  background: #904c96;
  border-radius: 50%;
  transition: transform .2s ease;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 14px;
  cursor: pointer;
}

.ui-checkbox--enter input:checked + label::after {
  transform: scale(1) rotate(-45deg);
}

.ui-checkbox--enter label {
  padding-left: 32px;
}

.ui-checkbox--enter label:before {
  border-radius: 0;
  size: 22px;
}

.ui-checkbox--enter label:after {
  left: 4px;
  top: 4px;
  border-radius: 0;
  border: 3px solid #f06eaa;
  background: none;
  size: 0;
  width: 12px;
  height: 6px;
  transform: scale(0) rotate(-45deg);
  border-right: none;
  border-top: none;
}

.ui-checkbox--enter label span {
  padding-left: 0;
  font-size: 16px;
}

