@import url('https://fonts.googleapis.com/css?family=Montserrat:500&subset=cyrillic');

@font-face {
    font-family: 'Minion Pro';
    src: url('../fonts/MinionPro/MinionPro-Bold.eot');
    src: url('../fonts/MinionPro/MinionPro-Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/MinionPro/MinionPro-Bold.woff2') format('woff2'),
    url('../fonts/MinionPro/MinionPro-Bold.woff') format('woff'),
    url('../fonts/MinionPro/MinionPro-Bold.ttf') format('truetype'),
    url('../fonts/MinionPro/MinionPro-Bold.svg#MinionPro-Bold') format('svg') {}
    font-weight: bold;
    font-style: normal; }

