.blog {
  padding-top: 2px;
  &__image {
    max-width: 1040px;
    width: 100%; }
  &__content {
    padding-top: 68px;
    h2 {
      font-size: 30px;
      color: #000000;
      line-height: 1.2;
      font-family: $book;
      letter-spacing: 0.8px; }
    h3 {
      font-size: 24px;
      font-family: $book;
      margin-top: 47px;
      letter-spacing: 0.65px; }
    .pink {
      color: #f06eaa; }
    p {
      margin-top: 26px;
      font-family: $book;
      font-size: 16px;
      letter-spacing: 0.45px;
      line-height: 1.5;
      a {
        color: #f06eaa;
        font-family: $book; }
      &.tags {
        margin-top: 50px; } }
    .blog__image {
      max-width: 800px;
      margin-top: 40px; } }

  &__video-container {
    max-width: 800px;
    margin-top: 40px; }

  &__video {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-width: 0;
      outline-width: 0; } }

  ul {
    margin: 0;
    padding: 0 0 0 50px;

    li {
      display: list-item;
      position: relative;
      &::before {
        content: "–";
        color: $pink;
        font-size: 24px;
        display: inline-block;
        top: -1px;
        left: -30px;
        position: absolute; } } } }

@include r($tablet) {
  .blog {
    &__content {
      padding-top: 40px;
      h2 {
        font-size: 24px; }
      h3 {
        margin-top: 30px;
        font-size: 20px; }
      p {
        margin-top: 20px;
        &.tags {
          margin-top: 35px; } }
      .blog__image {
        margin-top: 25px; } }

    &__video-container {
      margin-top: 25px; }

    ul {
      padding: 0 0 0 20px;
      li {
        &:before {
          font-size: 20px;
          left: -25px;
          top: 1px; } } } } }
