/* HEADER */
.header {
  // position: fixed
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  background: #fff;
  height: auto;
  z-index: 100;
  transform: translate3d(0,0,0);
  // border-bottom: 1px solid #b3b3b3
  // box-shadow: 0px 0px 5px rgba(0,0,0,0.1)
  .container {
 }    // outline: 1px solid red
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 34px 0 32px;
    position: relative; }
  .hamburger {
    display: none; }
  &__left {
    a {
      font-size: 14px;
      color: #000;
      font-family: $book;
      margin-right: 36px;
      text-transform: uppercase;
      position: relative;
      &:before {
        width: 0;
        height: 1px;
        position: absolute;
        display: block;
        content: '';
        left: 0;
        bottom: -2px;
        background: #000;
        transition: all $transitionSpeed $ease; }
      &:hover {
        &:before {
          width: 100%; } }
      &:last-child {
        margin-right: 20px; } } }
  &__logotype {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 32px;
    font-family: $logo;
    color: #904c96;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    line-height: 1;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    img {
      height: 41px;
      width: auto;
      margin-right: 15px;
      margin-top: -3px;
      margin-bottom: -10px; }
    span {
      margin-top: -10px; } }

  &__right {
    display: flex;
    align-items: center; }

  &__rules {
    display: inline-block;
    padding: 8px 15px;
    line-height: 1;
    font-size: 14px;
    text-transform: uppercase;
    color: #000;
    font-family: $book;
    border: 1px solid #e1e1e1;
    border-radius: 3px;
    margin-right: 16px;
    transition: all $transitionSpeed $ease;
    &:hover {
      background: #904c96;
      color: #fff;
      border-color: #904c96; } }

  &__icons {
    a {
      margin-left: 24px;
      display: inline-block;
      position: relative;
      img {
        height: 25px;
        width: auto; }
      &.show-points {
        .header__icons-points {
          top: -10px;
          opacity: 1; } } }
    &-points {
      position: absolute;
      right: -10px;
      top: -100px;
      background: #f06eaa;
      border-radius: 50%;
      font-size: 12px;
      font-family: $book;
      line-height: 1;
      color: #fff;
      padding: 3px;
      min-width: 18px;
      min-height: 18px;
      text-align: center;
      transition: all $transitionSpeed $ease;
      opacity: 1; } }

  nav {
    border-top: 1px solid #eee8ee;
    border-bottom: 1px solid #e7dbde;

    .header__icons {
      display: none; }

    .header__left {
      display: none; } }

  &__nav-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 18px 30px 14px 0;
    padding: 0 30px 0 0;
    position: relative;
    .header__rules {
      display: none; }
    ul {
      padding: 0;
      margin: 0; }
    li {
      font-size: 0;
      margin: 0;
      padding: 0; } }
  &__nav-link {
    padding-top: 18px;
    padding-bottom: 16px;
    font-size: 14px;
    color: #000;
    font-family: $demi;
    text-transform: uppercase;
    position: relative;
    margin-right: 25px;
    display: inline-block;
    span {
      position: relative;
      &:before {
        width: 0;
        height: 1px;
        position: absolute;
        display: block;
        content: '';
        left: 0;
        bottom: -2px;
        background: #000;
        transition: all $transitionSpeed $ease; } }
    &:hover {
      span {
        &:before {
          width: 100%; } } } }

  &__sale {
    margin-left: 14px;
    &:before {
      display: none !important; } }
  &__search {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0; }
  .search {
    &__box {
      // background-color: #1B1464
      padding: 2px;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      margin-left: auto;
      position: relative;
      &:before {
        position: absolute;
        display: block;
        content: '';
        right: 0;
        top: 0;
        height: 100%;
        background: #fff;
        width: 0;
        transition: all 0.5s $ease; }

      // &:hover
      &.active {
        &:before {
          width: 2000px; }
        .search__field {
          width: 380px;
          border-color: #e1e1e1;
          color: #000;
          padding: 7px 10px; }


        .search__btn {
          background-color: #fff; }

        .search__close {
          opacity: 1;
          pointer-events: all; } } }

    &__field {
      outline: none;
      border: none;
      background: transparent;
      color: #fff;
      font-size: 14px;
      font-family: $demi;
      margin: 0;
      width: 0;
      transition: all ease-in 400ms;
      border: 1px solid #fff;
      background: #fff;
      padding: 7px 0;
      transition: all $transitionSpeed $ease;
      position: relative;
      z-index: 2;
      &::placeholder {
        color: #e1e1e1; } }

    &__close {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -23px;
      border: none !important;
      font-size: 0;
      opacity: 0;
      transition: all $transitionSpeed $ease;
      pointer-events: none;
      img {
        width: 15px;
        height: 15px; }
      &:before {
        display: none !important; } }


    &__btn {
      border: none;
      outline: none;
      background-color: transparent;
      color: #000;
      height: 24px;
      width: 24px;
      display: grid;
      place-items: center;
      font-size: 18px;
      border-radius: 50%;
      transition: all ease-out 300ms;
      transition-delay: 380ms;
      cursor: pointer;
      text-align: center;
      padding: 0;
      position: absolute;
      right: 5px;
      z-index: 2;

      img {
        width: 18px;
        height: 18px;
        margin: 0 auto;
        &.active {
          position: absolute;
          opacity: 0;
          top: 3px;
          left: 3px; } }
      &.active {
        img {
          opacity: 0;
          &.active {
            opacity: 1; } } } } }

  .header__sale {
    display: none; }

  &__enter {
    background: #fff;
    padding: 26px 40px 32px;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 0 40px rgba(0,0,0,0.4);
    position: absolute;
    top: 100%;
    z-index: 2;
    right: 0;
    opacity: 0;
    pointer-events: none;
    transition: all $transitionSpeed $ease;
    &.is-open {
      opacity: 1;
      pointer-events: all; }
    &-head {
      display: flex;
      align-items: flex-end;
      padding-bottom: 7px; }
    p {
      font-size: 18px;
      text-transform: uppercase;
      font-family: $demi;
      color: #000000;
      line-height: 1; }
    a {
      display: inline-block;
      margin-left: auto;
      color: #000000;
      font-size: 16px;
      font-family: $book;
      line-height: 1; }
    .input-container {
      input {
        border: 1px solid #e1e1e1;
        padding: 9px 19px 10px;
        letter-spacing: 1.5px;
        margin-top: 20px; }
      .btn {
        margin-top: 20px;
        background: #904c96;
        border-color: #904c96;
        &:hover {
          background: #56205c;
          border-color: #56205c; } } }
    &-bottom {
      display: flex;
      align-items: center;
      padding-top: 16px; } }

  &__sizes {
    // overflow: auto
    // max-height: 750px
    padding-bottom: 40px;
    border-bottom: 1px solid #e1e1e1;
    left: 50%;
    transform: translateX(-50%) translateY(-150%);
    width: 100%;
    background: #fff;
    position: absolute;
    transition: all $transitionSpeed $ease;
    opacity: 0;
    &.is-open {
      opacity: 1;
      transform: translateX(-50%) translateY(0); }
    .sizes__bg-rows {
      left: -50%;
      width: 200%; }
    &-head {
      display: flex;
      align-items: center;
      padding: 20px 0; }
    &-buttons {
      a {
        display: inline-block;
        font-size: 13px;
        font-family: $demi;
        text-transform: uppercase;
        color: #000000;
        padding: 12px 20px;
        line-height: 1;
        border-right: 1px solid #fff;
        background: #faf3fa;
        transition: all $transitionSpeed $ease;
        &:lasat-child {
          border-right: none; }
        &:hover {
          background: #ecdeec; }
        &.is-active {
          color: #fff;
          background: #bf8fc4; } } }
    &-close {
      display: block;
      margin-left: auto;
      width: 30px;
      height: 30px;
      background: url(../img/close.svg) center center no-repeat;
      background-size: 15px 15px;
      opacity: 1;
      cursor: pointer; }
    &-tab {
      display: none;
      &.is-active {
        display: block; }
      .sizes__table {
        margin-top: 0;
        overflow: visible;
        border: none; } }
    &-button {
      text-align: center;
      position: relative;
      z-index: 2;
      a {
        display: inline-block;
        padding: 20px;
        max-width: 585px;
        width: 100%;
        background: #904c96;
        font-family: $book;
        font-size: 18px;
        color: #fff;
        cursor: pointer;
        transition: all $transitionSpeed $ease;
        &:hover {
          background: #7d4482; } } } }

  &__busts {
    padding: 30px 0;
    border-bottom: 1px solid #e1e1e1;
    left: 0;
    top: 100%;
    width: 100%;
    background: #fff;
    position: fixed;
    transition: all $transitionSpeed $ease;
    opacity: 0;
    max-height: calc(100vh - 150px);
    overflow: auto;
    pointer-events: none;
    transform: translateY(-30px);
    &.is-open {
      opacity: 1;
      transform: translateY(0);
      pointer-events: all; }
    &-grid {
      display: flex; }
    &-buttons {
      display: none; }
    &-all {
      width: 44%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
    &-types {
      width: 26%; }
    &-close {
      display: block;
      margin-left: auto;
      width: 30px;
      height: 30px;
      background: url(../img/close.svg) center center no-repeat;
      background-size: 15px 15px;
      opacity: 1;
      cursor: pointer;
      margin-top: 5px; }
    &-item {
      width: 48%;
      max-width: 250px;
      display: flex;
      align-items: flex-start;
      overflow: hidden;
      margin: 13px 0;
      &:hover {
        img {
          opacity: 0;
          &.hover {
            opacity: 1; } }
        .header__busts-image-hover {
          opacity: 1;
          img {
            opacity: 1; } } } }
    &-image {
      min-width: 50px;
      width: 50px;
      height: auto;
      font-size: 0;
      position: relative;
      margin-right: 15px;
      img {
        width: 50px;
        height: auto;
        transition: all $transitionSpeed $ease;
        &.hover {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          z-index: 2;
          opacity: 0; } }
      &-hover {
        width: 100%;
        height: 100%;
        background: #fff;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        transition: all $transitionSpeed $ease; } }
    &-info {
      p {
        font-size: 14px;
        font-family: $book;
        color: #959595;
        margin-top: 5px;
        line-height: 1.3;
        &.title {
          font-size: 16px;
          font-family: $demi;
          color: #000;
          margin-top: 0; } } }
    &-types {
      padding-top: 10px;
      padding-left: 50px;
      p {
        font-size: 16px;
        color: #000;
        text-transform: uppercase;
        font-family: $demi; }
      ul {
        margin: 0;
        padding: 0; }
      li {
        display: block;
        margin-top: 18px; }
      a {
        font-size: 16px;
        font-family: $book;
        color: #000000; } } } }


@include r($wide) {
  .header {
    &__busts {
      max-height: calc(100vh - 107px); }
    &__nav-wrapper {
      padding: 0 30px 0 0; }
    &__nav-link {
      font-size: 11px;
      margin-right: 10px;
      padding-top: 13px;
      padding-bottom: 13px; }
    &__sale {
      img {
        height: 20px; } }
    .search {
      &__box {
        &:hover {
          .search__field {
            width: 150px;
            padding: 4px 10px; } } }
      &__field {
        padding: 4px 0; } }
    &__left {
      a {
        margin-right: 25px; } }
    &__wrapper {
      padding: 20px 0; }
    &__logotype {
      position: static;
      left: 300px;
      transform: translateX(0);
      font-size: 24px;
      img {
        height: 28px;
        margin-bottom: -5px; } }
    &__icons {
      a {
        img {
          height: 22px; } } }
    &__rules {
      font-size: 12px;
      padding: 6px 12px; } } }

@include r($desktop) {
  .header {
    border-bottom: 1px solid #e1e1e1;
    .hamburger {
      display: block;
      order: 4;
      padding: 0;
      margin-right: 0px;
      width: 30px;
      height: 24px;
      opacity: 1;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      .hamburger-inner {
        border-radius: 0;
        background: #904c96;
        width: 30px;
        &:before,
        &:after {
          border-radius: 0;
          background: #904c96;
          width: 100%; }
        &:before {
          width: 30px; }
        &:after {} } }
    &__rules {
      display: none; }
    &__busts {
      top: 0;
      z-index: 2;
      max-height: calc(100vh - 64px);
      ul {
        border: none !important;
        width: 100% !important;
        margin: 0 !important;
        li {
          a {
            padding: 0 !important;
            font-size: 16px !important; } } } }
    &__nav-wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      padding: 0; }

    &__nav-link {
      span {
        &:before {
          display: none; } }

      .header__sale {
        display: none; }
      .header__rules {
        display: inline-block;
        font-size: 13px;
        margin-top: 20px;
        margin-bottom: 40px;
        &:before {
          display: none; } } }
    &__wrapper {
      position: relative;
      padding-right: 60px; }
    &__logotype {
      position: static;
      font-size: 20px;
      img {
        height: 24px;
        margin-bottom: -3px;
        margin-top: -4px; } }
    &__left {
      a {
        font-size: 13px; } }
    nav {
      position: absolute;
      top: 63px;
      width: 100%;
      background: #fff;
      transform: translateX(-100%);
      transition: all $transitionSpeed $ease;
      &.is-open {
        transform: translateX(0); }
      ul {
        width: calc(100% + 80px);
        margin: 0 -40px;
        border-top: 1px solid #e1e1e1;
        li {
          display: block;
          width: 100%;
          & > a {
            font-size: 14px;
            padding: 20px 40px;
            display: block;
            width: 100%;
            &:before {
              display: none; }
            &:hover {
              background: #fdebf3; } } } } }



    &__icons {
      margin-bottom: -4px;
      a {
        margin-left: 18px; } }

    &__search {
      position: static;
      order: -1;
      top: 0;
      transform: translateY(0);
      padding: 10px 0; }

    .search {
      &__field {
        padding: 7px 0;
        font-size: 14px;
        margin-left: 10px; }
      &__box {
        flex-direction: row-reverse;
        &:hover {
          .search__field {
            padding: 7px 10px;
            width: 280px; } } } } } }



@include r($tablet) {
  .header {
    .header__sale {
      display: block;
      font-size: 0;
      margin: 0;
      img {
        height: 25px; } }
    .container {
      padding: 0 20px; }
    &__wrapper {
      padding: 15px 0; }
    .hamburger {
      position: static;
      transform: translateY(0); }
    &__left {
      display: none; }
    &__logotype {
      a {
        font-size: 0; }
      img {
        width: 52px;
        height: 25px;
        margin: 0; }
      span {
        display: none; } }
    &__right {
      height: 24px; }
    &__icons {
      height: 24px;
      a {
        font-size: 0;
        margin: 0;
        img {
          height: 23px; }
        &:nth-child(2) {
          display: none; }
        &:nth-child(3) {
          display: none; } } }
    nav {
      top: 55px;
      overflow: auto;
      height: calc(100vh - 55px);
      padding-right: 0;
      width: 100%;
      .header__icons {
        display: block;
        margin: 0 -20px;
        padding: 17px 20px 14px;
        border-bottom: 1px solid #e1e1e1;
        height: auto;
        text-align: right;

        a {
          display: inline-block;
          margin-left: 30px;
          img {
            height: 26px; } } }

      .header__left {
        display: block;
        a {
          &:before {
            display: none; } } }

      .header__sale {
        display: none; }

      ul {
        margin: 0 -20px;
        width: calc(100% + 40px);
        border: none;
        li {
          a {
            padding: 20px; } } }
      .header__left {
        display: block;
        border-top: 1px solid #e1e1e1;
        width: calc(100% + 40px);
        margin: 0 -20px;
        padding: 0 20px;
        a {
          font-size: 14px;
          font-family: $book;
          padding: 30px 0 20px;
          display: inline-block; } } }
    &__search {
      width: 100%; }
    .search {
      &__close {
        display: none; }
      &__field {
        margin-left: 0;
        margin-right: 0;
        width: 100% !important;
        color: #000; }
      &__btn {
        margin-right: 0;
        min-width: 24px; }
      &__box {
        justify-content: flex-end;
        padding: 0;
        &:hover {
          .search__field {
            width: 100%; } } } }
    &__enter {
      top: calc(100% + 63px); }

    &__sizes {
      // overflow: auto
      // max-height: 750px
      border-top: 1px solid #e1e1e1;
      padding-bottom: 30px;
      border-bottom: 1px solid #e1e1e1;
      &-head {
        display: flex;
        align-items: center;
        padding: 20px 0; }
      &-buttons {
        position: relative;
        width: 100%;
        z-index: 3;
        min-height: 46px;
        &:after {
          display: block;
          position: absolute;
          content: '';
          width: 5px;
          height: 5px;
          border: 2px solid #fff;
          top: 50%;
          right: 15px;
          z-index: 3;
          transform: translateY(-50%) rotate(45deg);
          border-top: none;
          border-left: none;
          transition: all $transitionSpeed $ease; }
        &:hover {
          &:after {
            top: 55%;
            transform: translateY(-50%) rotate(225deg); }
          a {
            pointer-events: all;
            opacity: 1;
            &:nth-child(1) {
              top: 0; }
            &:nth-child(2) {
              transform: translateY(47px); }
            &:nth-child(3) {
              transform: translateY(94px); } } }

        a {
          z-index: 2;
          display: block;
          font-size: 14px;
          text-align: center;
          color: #fff;
          background: #904c96;
          padding: 16px 20px;
          line-height: 1;
          border-right: none;
          position: absolute;
          left: 0;
          top: 0;
          opacity: 0;
          pointer-events: none;
          width: 100%;
          border-bottom: 1px solid #fff;
          &:hover {
            background: #7d4482; }
          &.is-active {
            color: #fff;
            background: #904c96;
            top: 0;
            opacity: 1;
            pointer-events: all;
            z-index: 2;
            border-bottom: none; } } }

      &-close {
        display: none; }
      &-tab {
        .sizes__table {
          overflow: hidden;
          overflow-x: auto;
          height: auto; } }
      &-button {
        margin-top: 15px;
        a {
          font-size: 18px;
          padding: 15px; } } }

    &__busts {
      top: -64px;
      height: 100%;
      z-index: 2;
      padding: 0 0 30px;
      border-top: 1px solid #e1e1e1;
      border-bottom: 1px solid #e1e1e1;
      max-height: calc(100vh - 55px);
      position: absolute;
      margin: 0 -20px;
      width: calc(100% + 40px);
      &-buttons {
        display: flex;
        border-bottom: 1px solid #e1e1e1;
        width: calc(100% + 40px);
        margin: 0 -20px;
        a {
          width: 50%;
          display: inline-block;
          font-size: 15px;
          text-transform: uppercase;
          font-family: $demi;
          text-align: center;
          padding: 12px;
          &.is-hidden {
            display: none; } } }
      &-all {
        width: 100%; }

      &-close {
        display: none; }

      &-types {
        display: none;
        padding-left: 0;
        width: 100%;
        p {
          display: none; } }

      &-block {
        display: none;
        padding: 15px 0 5px;
        &.is-active {
          display: flex; } } }

    &__busts-types {
      ul {
        width: 100% !important;
        margin: 0 !important; }
      a {
        font-size: 16px !important;
        padding: 0 !important; } }

    &__busts-item {
      padding: 0 !important; } } }

@include r($mobile) {
  .header__busts {
    &-item {
      width: 100%;
      max-width: 100%; } } }

.search-site__hint {
  position: absolute;
  top: 33px;
  right: 2px;
  background: #fff;
  padding: 0;
  width: calc(100% - 4px);
  opacity: 0;
  pointer-events: none;
  transition: all $transitionSpeed $ease;
  &.is-active {
    opacity: 1;
    pointer-events: all; } }

@media only screen and (max-width: 1260px) {
  .search-site__hint {
    border-top: none;
    z-index: 0; } }

@media only screen and (max-width: 767px) {
  .search-site__hint {
    display: none; } }

.search-site__hint--error {
  z-index: 2;
  padding-top: 18px;
  padding-bottom: 18px;
  border-top: 0;
  p {
    line-height: 1;
    color: rgba(254, 39, 39, 0.85);
    font-size: 14px;
    font-weight: 500; } }

.search-site__wrapper {
  overflow: hidden;
  width: 100%;
  padding: 10px 0; }

.search-site__scroll {
  max-height: 250px;
  overflow: hidden;
  overflow-y: scroll;
  width: calc(100% + 26px);
  padding-right: 11px; }


.search-site__item {
  width: 100%;
  padding: 10px 30px;
  display: inline-block;
  font-size: 15px;
  color: #000;
  font-family: $book;
  margin-right: 0;
  text-transform: none !important;
  &:hover {
    background: #fdebf3; }
  &:before {
    display: none !important; } }

.search-site__loader {
  position: absolute;
  right: 35px;
  top: 7px;
  transition: all $transitionSpeed $ease;
  opacity: 0;
  padding: 10px 5px;
  z-index: 100;
  &.is-loading {
    opacity: 1; } }

.search__box {
  input:focus ~ .search-site__loader, &:hover .search-site__loader {
    background: #fff; }
  .loader {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 auto; }
  .loader__item {
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    border-radius: 50%;
    font-size: 6px;
    height: 4px;
    width: 4px;
    background: #f06eaa;
    animation: loadingFade 1s infinite;
    &:nth-child(1) {
      -webkit-animation-delay: 0s;
      animation-delay: 0s; }
    &:nth-child(2) {
      -webkit-animation-delay: 0.1s;
      animation-delay: 0.1s; }
    &:nth-child(3) {
      -webkit-animation-delay: 0.3s;
      animation-delay: 0.3s; } } }

@-webkit-keyframes loadingFade {
  0% {
    opacity: 0; }

  50% {
    opacity: 0.8; }

  100% {
    opacity: 0; } }


@keyframes loadingFade {
  0% {
    opacity: 0; }

  50% {
    opacity: 0.8; }

  100% {
    opacity: 0; } }
