// UI


.btn {
  font-size: 16px;
  padding: 15px;
  max-height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: normal;
  background: #f06eaa;
  color: #fff;
  transition: all $transitionSpeed $ease;
  font-family: $book;
  // border-radius: 5px
  border-color: #f06eaa;
  &:hover {
    background: #de619b;
    border-color: #de619b; } }

