      //   width: 6px
      //   height: 6px
      //   border: 1px solid #e4132f
      //   background: #fff
      //   border-left: none
      //   border-bottom: none
      //   transform: rotate(135deg)
      //   bottom: -4px
      //   left: 13px
      // &:after
      //   display: block
      //   position: absolute
      //   content: ''
      //   width: 100%
      //   height: 2px
      //   bottom: 0
      //   left: 0
 }      //   background: #fff
    input,
    textarea {
      border-color: #f06eaa !important; } }
  input,
  textarea {
    border: 1px solid #fff;
    font-size: 15px;
    // font-family: $light
    font-family: $book;
    padding: 14px;
    // border-radius: 6px
    color: #333;
    transition: all $transitionSpeed $ease;
    width: 100%;
    line-height: normal;
    background: #fff;
    resize: none;
    &::-webkit-input-placeholder {
      color: #959595; }
    &::-moz-placeholder {
