        border-color: #de619b; } } } }


// Checkbox
.ui-checkbox {
  position: relative;
  input {
    display: none;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    &:checked {
      + label {
        &::before {
          border-color: #904c96; }
        &::after {
          transform: translateY(-50%) scale(1); } } } }
  label {
    display: block;
    width: 100%;
    position: relative;
    cursor: pointer;
    padding-left: 26px;
    font-size: 16px;
    color: #000;
    font-family: $book;
    letter-spacing: 0.8px;
    &::before {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      content: ' ';
      size: 16px;
      background-color: white;
      border: 1px solid #e1e1e1;
      border-radius: 50%;
      cursor: pointer;
      vertical-align: middle;
      cursor: pointer;
      transition: all $transitionSpeed ease-in-out; }
    &::after {
      position: absolute;
      left: 4px;
      top: 50%;
      transform: translateY(-50%) scale(0);
      display: inline-block;
      content: ' ';
      size: 10px;
      background: #904c96;
      border-radius: 50%;
      // background-image: svg-load('../../images/svg/checkbox.svg')
      // background-repeat: no-repeat
      // background-position: center
      transition: transform .2s ease; }
    span {
      display: inline-block;
      padding-left: 35px;
      vertical-align: top;
      line-height: 1.3;
      font-size: 14px;
      cursor: pointer; } }
  &--enter {
    input {
      &:checked {
        + label {
          &::after {
            transform: scale(1) rotate(-45deg); } } } }
    label {
      padding-left: 32px;
      &:before {
        border-radius: 0;
        size: 22px; }
      &:after {
        left: 4px;
        top: 4px;
        border-radius: 0;
        border: 3px solid #f06eaa;
        background: none;
        size: 0;
        width: 12px;
        height: 6px;
        transform: scale(0) rotate(-45deg);
        border-right: none;
        border-top: none; }
      span {
        padding-left: 0;
        font-size: 16px; } } } }
