.head {
  padding: 33px 0;
  &__title {
    font-family: $demi;
    font-size: 30px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    line-height: 1.2;
    &--middle {
      text-align: center;
      font-size: 18px;
      margin-top: 80px; } }
  &__grid {
    display: flex;
    align-items: flex-end;
    .head__title {
      padding-right: 15px; } }
  &__icons {
    margin-left: auto;
    padding-left: 15px;
    font-size: 0;
    display: flex;
    padding-bottom: 3px;

    a {
      display: flex;
      align-items: center;
      font-size: 0;
      margin-left: 37px;
      span {
        font-size: 16px;
        color: #000;
        font-family: $book;
        position: relative;
        letter-spacing: 0.8px;
        &:before {
          position: absolute;
          display: block;
          content: '';
          width: 0;
          height: 1px;
          left: 0;
          bottom: -1px;
          background: #000;
          transition: all $transitionSpeed $ease; }
        &:hover {
          &:before {
            width: 100%; } } } }

    img {
      height: 20px;
      width: auto;
      margin-right: 8px; } }

  &__list {
    padding-top: 25px;
    p {
      font-size: 14px;
      color: #000;
      font-family: $book;
      line-height: 1.3;
      margin-top: 15px; } }

  &__form {
    margin-top: 40px;
    display: flex;
    max-width: 700px;
    width: 100%;
    .input-container {
      width: 100%;
      input {
        border: 1px solid #e1e1e1;
        padding: 9px 19px 10px;
        letter-spacing: 1.5px; } }
    .btn {
      background: #904c96;
      border-color: #904c96;
      padding: 9px;
      width: 100%;
      max-width: 120px;
      font-size: 16px;
      cursor: pointer;
      margin-left: -1px;
      position: relative;
      z-index: 2;
      cursor: pointer;
      &:hover {
        background: #56205c;
        border-color: #56205c; } } }

  &__row {
    display: flex;
    align-items: center;
    line-height: 1;
    margin-top: 22px;
    &-date {
      font-size: 12px;
      color: #959595;
      margin-right: 19px; }
    &-views {
      display: flex;
      align-items: center;
      font-size: 12px;
      line-height: 0.9;
      img {
        height: 10px;
        margin-right: 6px; } } }
  &__back {
    font-family: $book;
    font-size: 17px;
    color: #f06eaa;
    line-height: 1;
    margin-top: 34px;
    display: inline-block;
    span {
      display: inline-block;
      width: 8px;
      height: 8px;
      border: 2px solid #f06eaa;
      transform: rotate(45deg);
      border-top: none;
      border-right: none;
      margin-right: 6px;
      margin-left: 1px; } } }



@include r($tablet) {
  .head {
    padding: 35px 0 26px;
    &__title {
      font-size: 24px;
      letter-spacing: 0.9px; }
    &__grid {
      flex-wrap: wrap;
      .head__title {
        padding-right: 0; } }
    &__icons {
      width: 100%;
      margin-left: 0;
      flex-direction: column;
      padding-bottom: 0;
      padding-left: 0;
      padding-top: 3px;

      a {
        margin-left: 0;
        margin-top: 20px; } }
    &__icons-icon {
      min-width: 29px;
      text-align: center; } } }
